    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: no-repeat center/cover url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    color: #F5E9E2;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

nav {
    padding-top: 15px;
    padding-bottom: 15px;
    display: flex;
    justify-content: center;
    gap: 30px;
    background-color: #BFA2DB9f;
}

nav > button {
    width: 120px;
    height: 40px;
    border: none;
    border-radius: 5px;
    background-color: #8F9779;
    color: #F5E9E2;
    font-size: 14px;

    &:hover {
        background-color: #A3B3A3;
    }
}

footer {
    min-height: 50px;
    text-align: center;
    margin-top: auto;
    align-content: center;
    background-color: #BFA2DB9f;
    font-size: 14px;
}

a {
    text-decoration: none;
    color: #4A4A4A;

    &:visited {
        color: #4A4A4A;
    }
}

img {
    width: 400px;
    height: 600px;
}

.content {
    flex: 1;
    justify-items: center;
    margin-top: 5%;
}

.container {
    margin: 20px;
    padding: 20px;
    background: #bfA2DB9f;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    border-radius: 15px;
    font-size: 18px;
}

.info-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.hours,
.location,
.contact-info,
.about {
    justify-items: center;
    line-height: 1.5;
    font-size: 18px;
}

.menu-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
}

.menu-item {
    background-color: #8F9779;
    height: 200px;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 30px;
    text-align: center;
